<template>
    <!-- footer -->
    <footer id="footer">
        <div class="inner text_center">
            <p>※ 상업적 목적 없이 포트폴리오용으로 제작된 페이지입니다.</p>
            <p>© Copyright 2024 Gayeong Kim All Rights Reserved</p>
        </div>
    </footer>
    <!-- quick -->
     <div @click="scrollToTop"
        :class="{active : isActive}"
        id="quick"
        class="flex flex_center flex_a_center">
        <button class="top" type="button">TOP</button>
     </div>
</template>

<script setup>
import { ref, onMounted, onUnmounted } from 'vue'

let isActive = ref(false);

let scrollToTop = () => {
    window.scrollTo({
        top:0,
        behavior: 'smooth',
    })
}
let handleScroll = () => {
    isActive.value = window.scrollY > 100;
}

// 컴포넌트가 마운트되면 스크롤 이벤트 리스너 추가
onMounted(() => {
    window.addEventListener('scroll', handleScroll);
});

// 컴포넌트가 언마운트되면 스크롤 이벤트 리스너 제거
onUnmounted(() => {
    window.removeEventListener('scroll', handleScroll);
});

</script>